import React, { useEffect, useState } from "react";
import {
  Grid,
  Card,
  CardContent,
  Typography,
  TextField,
  MenuItem,
  Button,
} from "@mui/material";
import { LineChart, BarChart, PieChart } from "@mui/x-charts";
import axios from "axios";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";

export default function ChartsPanel() {
  // --- Data States ---
  const [items, setItems] = useState([]);
  const [filteredItems, setFilteredItems] = useState([]);
  const [categories, setCategories] = useState([]);
  const [statuses, setStatuses] = useState([]);

  // --- Filter States ---
  const [search, setSearch] = useState("");
  const [category, setCategory] = useState("All");
  const [status, setStatus] = useState("All");
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  // --- Pagination ---
  const [page, setPage] = useState(1);
  const pageSize = 10;

  // --- Fetch Data & Filters from backend ---
  useEffect(() => {
    axios.get("http://localhost:4000/items").then((res) => {
      setItems(res.data.items);
      setFilteredItems(res.data.items);
    });
    axios.get("http://localhost:4000/filters").then((res) => {
      setCategories(res.data.categories);
      setStatuses(res.data.statuses);
    });
  }, []);

  // --- Apply Filters ---
  useEffect(() => {
    let temp = [...items];

    if (search) {
      const s = search.toLowerCase();
      temp = temp.filter(
        (i) =>
          i.name.toLowerCase().includes(s) ||
          i.type.toLowerCase().includes(s)
      );
    }

    if (category !== "All") temp = temp.filter((i) => i.category === category);
    if (status !== "All") temp = temp.filter((i) => i.status === status);

    if (startDate)
      temp = temp.filter((i) => new Date(i.date) >= new Date(startDate));
    if (endDate)
      temp = temp.filter((i) => new Date(i.date) <= new Date(endDate));

    setFilteredItems(temp);
    setPage(1); // reset page when filters change
  }, [search, category, status, startDate, endDate, items]);

  // --- Chart Aggregation ---
  const salesByDate = {};
  const monthlyCounts = {};
  const categoryCounts = {};

  filteredItems.forEach((it) => {
    const d = new Date(it.date);

    // Daily sales for line chart
    const day = d.toISOString().slice(0, 10);
    salesByDate[day] = (salesByDate[day] || 0) + (it.amount || 0);

    // Monthly sales for bar chart
    const month = `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(
      2,
      "0"
    )}`;
    monthlyCounts[month] = (monthlyCounts[month] || 0) + (it.amount || 0);

    // Category distribution for pie chart
    categoryCounts[it.category] = (categoryCounts[it.category] || 0) + 1;
  });

  const lineLabels = Object.keys(salesByDate).sort();
  const lineValues = lineLabels.map((k) => salesByDate[k]);

  const barLabels = Object.keys(monthlyCounts).sort();
  const barValues = barLabels.map((k) => monthlyCounts[k]);

  const pieData = Object.keys(categoryCounts).map((cat) => ({
    id: cat,
    label: cat,
    value: categoryCounts[cat],
  }));

  // --- Pagination Data ---
  const pagedItems = filteredItems.slice((page - 1) * pageSize, page * pageSize);

  // --- Reset Filters ---
  const resetFilters = () => {
    setSearch("");
    setCategory("All");
    setStatus("All");
    setStartDate(null);
    setEndDate(null);
  };

  return (
    <div>
      {/* --- Filters Panel --- */}
      <Card sx={{ mb: 2, p: 2 }}>
        <Grid container spacing={2}>
          <Grid item xs={12} md={3}>
            <TextField
              label="Search"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              select
              label="Category"
              value={category}
              onChange={(e) => setCategory(e.target.value)}
              fullWidth
            >
              {categories.map((c) => (
                <MenuItem key={c} value={c}>
                  {c}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              select
              label="Status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              fullWidth
            >
              {statuses.map((s) => (
                <MenuItem key={s} value={s}>
                  {s}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item xs={12} md={2}>
            <DatePicker
              label="Start Date"
              value={startDate}
              onChange={(newVal) => setStartDate(newVal)}
              renderInput={(params) => <TextField {...params} fullWidth />}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <DatePicker
              label="End Date"
              value={endDate}
              onChange={(newVal) => setEndDate(newVal)}
              renderInput={(params) => <TextField {...params} fullWidth />}
            />
          </Grid>
          <Grid item xs={12} md={1}>
            <Button variant="outlined" onClick={resetFilters} fullWidth>
              Reset
            </Button>
          </Grid>
        </Grid>
      </Card>

      {/* --- Charts Panel --- */}
      <Grid container spacing={2} sx={{ mb: 2 }}>
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="subtitle1">Sales Over Time</Typography>
              <div style={{ height: 250 }}>
                <LineChart
                  xAxis={[{ data: lineLabels }]}
                  series={[{ data: lineValues, label: "Amount" }]}
                />
              </div>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="subtitle1">Monthly Comparison</Typography>
              <div style={{ height: 250 }}>
                <BarChart
                  xAxis={[{ data: barLabels }]}
                  series={[{ data: barValues, label: "Amount" }]}
                />
              </div>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="subtitle1">Category Distribution</Typography>
              <div style={{ height: 220 }}>
                <PieChart
                  series={[
                    {
                      data: pieData,
                      innerRadius: 30,
                      outerRadius: 100,
                    },
                  ]}
                />
              </div>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* --- Paginated Table --- */}
      <Card>
        <CardContent>
          <Typography variant="h6" sx={{ mb: 2 }}>
            Items
          </Typography>
          <table style={{ width: "100%", borderCollapse: "collapse" }}>
            <thead>
              <tr>
                <th>ID</th>
                <th>Name</th>
                <th>Date</th>
                <th>Status</th>
                <th>Amount</th>
                <th>Type</th>
                <th>Category</th>
              </tr>
            </thead>
            <tbody>
              {pagedItems.map((item) => (
                <tr key={item.id}>
                  <td>{item.id}</td>
                  <td>{item.name}</td>
                  <td>{item.date}</td>
                  <td>{item.status}</td>
                  <td>{item.amount}</td>
                  <td>{item.type}</td>
                  <td>{item.category}</td>
                </tr>
              ))}
            </tbody>
          </table>

          {/* --- Pagination Controls --- */}
          <div style={{ marginTop: 10 }}>
            <Button
              onClick={() => setPage((p) => Math.max(p - 1, 1))}
              disabled={page === 1}
            >
              Previous
            </Button>
            <span style={{ margin: "0 10px" }}>
              Page {page} of {Math.ceil(filteredItems.length / pageSize)}
            </span>
            <Button
              onClick={() =>
                setPage((p) =>
                  Math.min(p + 1, Math.ceil(filteredItems.length / pageSize))
                )
              }
              disabled={page === Math.ceil(filteredItems.length / pageSize)}
            >
              Next
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
